import React from 'react';
import { connect } from 'react-redux';
import { DAY_OF_THE_WEEK } from '@constants/time';
import { color } from '@styles';
import { updateDateFilter } from '@store/modules/filters';
import type { Dispatch, AnyAction } from 'redux';
import HeatmapChartPresenter from './Presenter';

const BASE_OPACITY = 0x1f;
const RANGE_SIZE = 2;
const RANGE_COUNT = 4;
const MAX_COUNT = RANGE_SIZE * RANGE_COUNT;

export interface HeatmapRange {
  from: number;
  to: number;
  color: string;
}

export interface HeatmapData {
  x: string;
  y: number;
  meta: {
    count: number;
    date: Date;
  };
}

export interface HeatmapSeries {
  name: string;
  data: HeatmapData[];
}

interface ReduxProps {
  updateDateFilter: (date: { start: Date; end: Date }) => Dispatch<AnyAction>;
}

interface Props {
  countOfDates: Map<string, number>;
}

function HeatmapChartContainer({
  countOfDates,
  updateDateFilter,
}: Props & ReduxProps) {
  const series: HeatmapSeries[] = Array.from(Array(7), (_, index) => ({
    name: DAY_OF_THE_WEEK[index],
    data: [],
  }));

  const today = new Date();
  const sundayOfThisWeek = today.getDate() - today.getDay();

  for (let day = 0; day < 7; day += 1) {
    const date = new Date();
    date.setDate(sundayOfThisWeek + day);

    for (let j = Math.floor(365 / 7); j >= 0; j -= 1) {
      const prevNWeek = new Date(date);
      prevNWeek.setDate(prevNWeek.getDate() - 7 * j);

      if (prevNWeek.getTime() > Date.now()) {
        continue;
      }

      const fileCount = countOfDates.get(prevNWeek.toDateString()) || 0;

      series[day].data.push({
        /**
         * x label이 보이기 위해서 정확히 몇개의 x값을 가진 항목이 열에 존재해야하는지는 정확히 모르겠음.
         * 요일이 7개이므로 1일부터 7일까지 x값을 주었더니 일단은 제대로 작동함.
         */
        x: prevNWeek.getDate() <= 7 ? `${prevNWeek.getMonth() + 1}월` : '',
        y: fileCount > MAX_COUNT ? MAX_COUNT : fileCount,
        meta: {
          count: fileCount,
          date: prevNWeek,
        },
      });
    }
  }

  series.reverse();

  const heatmapRanges: HeatmapRange[] = Array(RANGE_COUNT)
    .fill(null)
    .map((_, index) => {
      const times = index + 1;
      const opacity = (
        BASE_OPACITY + Math.floor(((0xff - BASE_OPACITY) / RANGE_COUNT) * times)
      ).toString(16);

      return {
        from: 1 + RANGE_SIZE * (times - 1),
        to: RANGE_SIZE * times,
        color: color.PRIMARY + opacity,
      };
    });

  return (
    <HeatmapChartPresenter
      updateDateFilter={updateDateFilter}
      heatmapRanges={heatmapRanges}
      series={series}
    />
  );
}

export default connect(undefined, {
  updateDateFilter,
})(HeatmapChartContainer);
