import React from 'react';
import { Link } from 'gatsby';
import { DAY_OF_THE_WEEK } from '@constants/time';
import { dateToYYYYMMDD } from '@utils/date';
import type { DateRange } from '@store/modules/filters';
import XIcon from '@assets/images/x.svg';
import { useDispatch } from 'react-redux';
import { updateDateFilter, updateExtFilter } from '@store/modules/filters';

import {
  ActivityFilterList,
  ActivityFilterItem,
  ActivityListTitle,
  ActivityListTitleCircle,
  ActivityListTitleContent,
  ActivityLayout,
  ActivityList,
  ActivityItem,
  ActivityItemTagItem,
  ActivityItemTagList,
  HighlightKeyword,
} from './Styles';

// 상수 파일로 분리
const BLANK = '';

interface Props {
  divideByDate: Map<number, MatchedFileType[]>;
  keyword: string;
  date: DateRange | null;
  ext: string | null;
  INCLUDE_KEYWORD: RegExp;
}

function ActivityPresenter({
  divideByDate,
  keyword,
  date,
  ext,
  INCLUDE_KEYWORD,
}: Props) {
  const dispatch = useDispatch();

  const dateFilterCloseLabel = (() => {
    if (!date) {
      return null;
    }

    return (
      <ActivityFilterItem>
        <p>
          {dateToYYYYMMDD(date.start)}
          {date.start.toDateString() !== date.end.toDateString()
            ? ` ~ ${dateToYYYYMMDD(date.end)}`
            : ''}
        </p>
        <button
          type="button"
          onClick={() => {
            dispatch(updateDateFilter(null));
          }}
        >
          <XIcon />
        </button>
      </ActivityFilterItem>
    );
  })();

  const extFilterCloseLabel = (() => {
    if (!ext) {
      return null;
    }

    return (
      <ActivityFilterItem>
        <p>{ext}</p>
        <button
          type="button"
          onClick={() => {
            dispatch(updateExtFilter(null));
          }}
        >
          <XIcon />
        </button>
      </ActivityFilterItem>
    );
  })();

  return (
    <ActivityLayout>
      <ActivityFilterList>
        {dateFilterCloseLabel}
        {extFilterCloseLabel}
      </ActivityFilterList>

      {[...divideByDate.entries()]
        .sort((a, b) => b[0] - a[0])
        .map(([time, files]) => {
          const date = new Date(time);

          return (
            <div key={date.getTime().toString()}>
              {/** dummy post list Item */}
              <ActivityList>
                <ActivityItem>ㅤ</ActivityItem>
              </ActivityList>

              {/** post list title */}
              <ActivityListTitle>
                <ActivityListTitleCircle>
                  {DAY_OF_THE_WEEK[date.getDay()]}
                </ActivityListTitleCircle>
                <ActivityListTitleContent>
                  <span>
                    {`${date.getFullYear()}년 ${
                      date.getMonth() + 1
                    }월 ${date.getDate()}일`}
                  </span>
                </ActivityListTitleContent>
              </ActivityListTitle>

              {/** post list */}
              <ActivityList>
                {files.map((file) => {
                  const { relativeDirectory, name, ext, markdownData } = file;

                  const relativePath = relativeDirectory
                    ? `${relativeDirectory}/${name + ext}`
                    : name + ext;

                  return (
                    <ActivityItem key={relativePath}>
                      <Link to={`/post/${relativePath}`}>
                        {relativePath
                          .split(INCLUDE_KEYWORD)
                          .map((value, index) => {
                            if (
                              keyword !== BLANK &&
                              INCLUDE_KEYWORD.test(value)
                            ) {
                              // highlight 조각은 유동적으로 달라질 일이 없어 index를 키값으로 사용함.
                              return (
                                <HighlightKeyword key={index}>
                                  {value}
                                </HighlightKeyword>
                              );
                            }

                            return value;
                          })}
                      </Link>
                      {markdownData && (
                        <ActivityItemTagList>
                          {markdownData.frontmatter.tag &&
                            markdownData.frontmatter.tag.map((name, index) => (
                              // 태그는 유동적으로 달라질 일이 없어 index를 키값으로 사용함.
                              <ActivityItemTagItem key={index}>
                                {`#${name}`
                                  .split(INCLUDE_KEYWORD)
                                  .map((value, index) => {
                                    if (
                                      keyword !== BLANK &&
                                      INCLUDE_KEYWORD.test(value)
                                    ) {
                                      // highlight 조각은 유동적으로 달라질 일이 없어 index를 키값으로 사용함.
                                      return (
                                        <HighlightKeyword key={index}>
                                          {value}
                                        </HighlightKeyword>
                                      );
                                    }

                                    return value;
                                  })}
                              </ActivityItemTagItem>
                            ))}
                        </ActivityItemTagList>
                      )}
                    </ActivityItem>
                  );
                })}
              </ActivityList>
            </div>
          );
        })}
    </ActivityLayout>
  );
}

export default ActivityPresenter;
