import React from 'react';
import { connect } from 'react-redux';
import { getRemovedHMS } from '@utils/date';
import type { DateRange } from '@store/modules/filters';
import ActivityPresenter from './Presenter';

interface ReduxProps {
  ext: string;
  date: DateRange;
  keyword: string;
}

interface Props {
  matchFiles: MatchedFileType[];
}

function ActivityContainer({
  matchFiles,
  ext,
  date,
  keyword,
}: Props & ReduxProps) {
  const INCLUDE_KEYWORD = new RegExp(`(${keyword})`, 'i');

  /**
   * 날짜별 포스트 분류
   */
  const divideByDate = new Map<number, MatchedFileType[]>();

  matchFiles.forEach((file) => {
    const {
      relativeDirectory,
      name,
      fields: {
        stampObject: { created },
      },
      markdownData,
    } = file;

    const fileDate = getRemovedHMS(created * 1000);
    const msDate = fileDate.getTime();

    // 확장자, 검색어, 날짜 정보로 필터링
    const fileExt = file.ext;

    const searchTarget =
      relativeDirectory +
      name +
      fileExt +
      (markdownData?.frontmatter.tag?.join(' ') || '');

    if (ext && fileExt !== ext) {
      return;
    }

    if (!INCLUDE_KEYWORD.test(searchTarget)) {
      return;
    }

    if (
      date &&
      !(
        getRemovedHMS(date.start).getTime() <= msDate &&
        msDate <= getRemovedHMS(date.end).getTime()
      )
    ) {
      return;
    }

    if (!divideByDate.has(msDate)) {
      divideByDate.set(msDate, []);
    }

    divideByDate.get(msDate)?.push(file);
  });

  divideByDate.forEach((files) => {
    files.sort(
      (a, b) => a.fields.stampObject.created - b.fields.stampObject.created
    );
  });

  return (
    <ActivityPresenter
      divideByDate={divideByDate}
      keyword={keyword}
      date={date}
      ext={ext}
      INCLUDE_KEYWORD={INCLUDE_KEYWORD}
    />
  );
}

export default connect(
  ({ filters: { ext, date, keyword } }) => ({
    ext,
    date,
    keyword,
  }),
  {}
)(ActivityContainer);
