import React from 'react';
import { connect } from 'react-redux';
import type { AnyAction, Dispatch } from 'redux';
import { updateExtFilter } from '@store/modules/filters';
import DonutChartPresenter from './Presenter';

interface ReduxProps {
  updateExtFilter: (ext: string) => Dispatch<AnyAction>;
}

interface Props {
  countOfExts: Map<string, number>;
}

function DonutChartContainer({
  countOfExts,
  updateExtFilter,
}: Props & ReduxProps) {
  const labels = [...countOfExts.keys()];
  const series = [...countOfExts.values()];

  const sum = series.reduce((acc, cur) => acc + cur, 0);

  return (
    <DonutChartPresenter
      labels={labels}
      series={series}
      sum={sum}
      updateExtFilter={updateExtFilter}
    />
  );
}

export default connect(undefined, {
  updateExtFilter,
})(DonutChartContainer);
