import React from 'react';
import type { AnyAction, Dispatch } from 'redux';
import loadable from '@loadable/component';
import { DonutChartLayout } from './Styles';

const LoadableChart = loadable(() => import('react-apexcharts'));

interface Props {
  labels: string[];
  series: number[];
  sum: number;
  updateExtFilter: (ext: string) => Dispatch<AnyAction>;
}

function DonutChartPresenter({ labels, series, sum, updateExtFilter }: Props) {
  return (
    <DonutChartLayout>
      <LoadableChart
        type="donut"
        series={series}
        options={{
          labels,
          legend: { show: false },
          chart: {
            id: 'extDonutGraph',
            fontFamily: 'GothicA1',
            events: {
              dataPointSelection: (e, chart, options) => {
                try {
                  const { dataPointIndex } = options;

                  const label = labels[dataPointIndex];

                  updateExtFilter(label);
                } catch (err) {
                  console.error(err);
                }
              },
            },
          },
          tooltip: {
            y: {
              formatter: (value) => `${Math.round((value / sum) * 1000) / 10}%`,
            },
          },
          plotOptions: {
            pie: {
              expandOnClick: false,
              donut: {
                size: '55%',
              },
            },
          },
          dataLabels: {
            formatter: (value, { seriesIndex }) => labels[seriesIndex],
          },
          states: {
            active: {
              filter: {
                type: 'none',
              },
            },
          },
        }}
      />
    </DonutChartLayout>
  );
}

export default DonutChartPresenter;
