import React from 'react';
import { DAY_OF_THE_WEEK } from '@constants/time';
import { color } from '@styles';
import loadable from '@loadable/component';
import type { Dispatch, AnyAction } from 'redux';
import type { HeatmapRange, HeatmapSeries } from './Containter';

const LoadableChart = loadable(() => import('react-apexcharts'));

interface Props {
  heatmapRanges: HeatmapRange[];
  series: HeatmapSeries[];
  updateDateFilter: (date: { start: Date; end: Date }) => Dispatch<AnyAction>;
}

function HeatmapChartPresenter({
  updateDateFilter,
  heatmapRanges,
  series,
}: Props) {
  return (
    <LoadableChart
      type="heatmap"
      options={{
        chart: {
          id: 'fileHeatMap',
          zoom: {
            enabled: false,
          },
          toolbar: {
            show: false,
          },
          fontFamily: 'GothicA1',
          events: {
            dataPointSelection: (e, chart, options) => {
              try {
                const {
                  w: {
                    globals: { selectedDataPoints },
                  },
                  dataPointIndex,
                } = options;

                const seriesIndex = selectedDataPoints.length - 1;

                const {
                  meta: { count, date },
                } = series[seriesIndex].data[dataPointIndex];

                const startDate = new Date(date);
                const endDate = new Date(date);

                updateDateFilter({ start: startDate, end: endDate });
              } catch (err) {
                console.error(err);
              }
            },
          },
        },
        states: {
          active: {
            filter: {
              type: 'none',
            },
          },
        },
        dataLabels: {
          enabled: false,
        },
        legend: {
          show: false,
        },
        plotOptions: {
          heatmap: {
            enableShades: false,
            radius: 3,
            colorScale: {
              ranges: [
                {
                  from: 0,
                  to: 0,
                  color: color.BORDER,
                },
                ...heatmapRanges,
              ],
            },
          },
        },
        grid: {
          show: false,
        },
        xaxis: {
          axisTicks: {
            show: false,
          },
          labels: {
            rotate: 0,
          },
          tooltip: {
            enabled: false,
          },
          crosshairs: {
            show: false,
          },
        },
        tooltip: {
          x: {
            show: false,
          },
          y: {
            formatter: (_, { seriesIndex, dataPointIndex }) => {
              const { data } = series[seriesIndex];

              const {
                meta: { count, date },
              } = data[dataPointIndex];

              return `${date.getFullYear()}년 ${
                date.getMonth() + 1
              }월 ${date.getDate()}일 ${
                DAY_OF_THE_WEEK[date.getDay()]
              }요일 : ${count}개`;
            },
            title: {
              formatter: () => '',
            },
          },
        },
      }}
      series={series}
      width={884}
      height={180}
    />
  );
}

export default HeatmapChartPresenter;
