import React from 'react';
import loadable from '@loadable/component';
import { getRelativeDate } from '@utils/date';
import { DAY_OF_THE_WEEK } from '@constants/time';
import { color } from '@styles';
import { useDispatch } from 'react-redux';
import { updateDateFilter } from '@store/modules/filters';
import { WeeklyActivityChartLayout } from './Style';

const LoadableChart = loadable(() => import('react-apexcharts'));

interface WeeklyActivityChartProps {
  countOfDates: Map<string, number>;
}

interface LineChartDataType {
  x: number;
  y: number;
}

interface LineChartSeriesType {
  data: LineChartDataType[];
}

function WeeklyActivityChart({ countOfDates }: WeeklyActivityChartProps) {
  const dispatch = useDispatch();

  const thisSunday = (() => {
    const date = new Date();

    date.setDate(date.getDate() - date.getDay());

    return date;
  })();

  const series: LineChartSeriesType[] = [{ data: [] }];

  for (let week = 0; week < 7; week += 1) {
    let count = 0;

    const sundayDate = getRelativeDate(thisSunday, -(week * 7));

    for (let day = 0; day < 7; day += 1) {
      const prevDate = getRelativeDate(thisSunday, -(week * 7) + day);

      count += countOfDates.get(prevDate.toDateString()) || 0;
    }

    series[0].data.push({
      x: sundayDate.getTime(),
      y: count,
    });
  }

  return (
    <WeeklyActivityChartLayout>
      <LoadableChart
        type="line"
        width="100%"
        height="100%"
        series={series}
        options={{
          chart: {
            id: 'activityWeeklyActivityChart',
            toolbar: {
              show: false,
              tools: {
                zoom: false,
                zoomout: false,
                pan: false,
              },
            },
            fontFamily: 'GothicA1',
            events: {
              markerClick: (
                event,
                chartContext,
                { seriesIndex, dataPointIndex, w }
              ) => {
                const { x, y } = series[seriesIndex].data[dataPointIndex];

                const startDate = new Date(x);
                const endDate = getRelativeDate(new Date(x), 7);

                dispatch(updateDateFilter({ start: startDate, end: endDate }));
              },
            },
          },
          stroke: {
            width: 3,
          },
          markers: {
            size: 4,
            colors: ['#ffffff'],
            strokeWidth: 3,
            strokeColors: color.PRIMARY,
            hover: {
              sizeOffset: 2,
            },
          },
          colors: [color.PRIMARY],
          xaxis: {
            tooltip: {
              enabled: false,
            },
            labels: {
              formatter: (value: string) => {
                const date = new Date(value);

                return `${date.getMonth() + 1}/${date.getDate()}`;
              },
            },
            axisTicks: {
              show: false,
            },
          },
          yaxis: {
            show: false,
          },
          dataLabels: {
            enabled: false,
          },
          grid: {
            padding: {
              top: 30,
              left: 30,
              right: 30,
              bottom: 30,
            },
            yaxis: {
              lines: {
                show: false,
              },
            },
            xaxis: {
              lines: {
                show: true,
              },
            },
          },
          tooltip: {
            x: {
              show: false,
            },
            y: {
              formatter: (_, { seriesIndex, dataPointIndex }) => {
                const { data } = series[seriesIndex] as LineChartSeriesType;
                const { x, y } = data[dataPointIndex];

                const date = new Date(x);

                return `${date.getFullYear()}년 ${
                  date.getMonth() + 1
                }월 ${date.getDate()}일 ${
                  DAY_OF_THE_WEEK[date.getDay()]
                }요일 : ${y}개`;
              },
              title: {
                formatter: () => '',
              },
            },
            marker: {
              show: false,
            },
          },
        }}
      />
    </WeeklyActivityChartLayout>
  );
}

export default WeeklyActivityChart;
